@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
    margin: 0;
    padding: 0;
    font-family: 'Open Sans', sans-serif;
}

body{
    transition: background-color 0.25s ease-out;
}

.App{
    align-items: center;
    justify-content: center;
    display: flex;
    flex-direction: column
}

#inv-input{
    opacity: 0;
    position: absolute;
}

.side{
    display: block;
}

.side.win{
    background-color: #2ecc71;
}

.dis{
    pointer-events: none;
}

.side__name{
    font-size: 29px;
    margin-bottom: 10px;
}

.side-wrapper{
    margin-top: 15px;
}

.side__wrapper{
    width: 327px;
    display: flex;
    justify-content: space-between;
}

.hero-select{
    display: inline-block;
    width: 60px;
    height: 70px;
    border: none;
    outline: none;
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center top;
    position: relative;
    image-rendering: optimizeSpeed;             /* STOP SMOOTHING, GIVE ME SPEED  */
    image-rendering: -moz-crisp-edges;          /* Firefox                        */
    image-rendering: -o-crisp-edges;            /* Opera                          */ /* Chrome (and eventually Safari) */
    image-rendering: pixelated; /* Chrome */
    image-rendering: optimize-contrast;         /* CSS3 Proposed                  */
    -ms-interpolation-mode: nearest-neighbor;
    background-size: 80%;
    transition: background-color 0.15s ease-out;
    border-radius: 3px;
}

.hero-select:hover{
    background-color: #ecf0f1;
}

.match{
    padding: 5px 15px;
    border-radius: 5px;
}

.match:hover{
    background-color: #ecf0f1;
}

.match-wrapper{
    display: flex;
    align-items: center;
}

.match-id{
    font-size: 20px;
    cursor: pointer;
    font-weight: bold;
}

.rank{
    width: 70px;
    height: 70px;
    background-image: url('https://gamepedia.cursecdn.com/dota2_gamepedia/a/ad/SeasonalRankTop2.png?version=89000c52fcb08e0ccafec82a886e26fc');
    margin-left: 10px;
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
}

#find{
    width: 200px;
    height: 50px;
    text-align: center;
    line-height: 50px;
    font-size: 22px;
    background-color: #c0392b;
    color: white;
    margin: 30px auto;
    cursor: pointer;
    border-radius: 4px;
    transition: background-color 0.25s ease-out;
}

#find:hover{
    background-color: #af372b;
}

.logo{
    width: 366px;
    font-size: 36px;
    font-weight: 500;
    margin: 30px 0 50px 0;
}

.logo__sub{
    font-size: 14px;
    font-weight: 400;
    color: #9c9c9c;
}

.hero-select select{
    position: absolute;
    bottom: 0;
    border: none;
    outline: none;
    background: none;
    cursor: pointer;
    height: 70px;
    padding-top: 47px;
    padding-left: 6px;
    -moz-appearance:none;
    -webkit-appearance:none;
    width: 54px;
}

.hero-select > select::-ms-expand{
    display: none;
}

#find.disabled{
    background-color: #ecf0f1;
    cursor: not-allowed;
}

.load{
    margin: 10px 0 10px 0;
}

.load{
    display: flex;
    align-items: center;
}

.load img{
    height: 150px;
}

.load__name{
    font-size: 32px;
    text-align: center;
    font-weight: 500;
    display: inline-block;
    margin-right: 20px;
}

footer{
    margin-top: 10px;
    font-size: 16px;
    color: #95a5a6;
    padding-bottom: 30px;
}

.exm{
    width: 366px;
    border-radius: 5px;
}

.PS-sub{
    background-color: #f5f5f5;
    padding: 10px;
    border-radius: 5px;
    color: #da0000;
    font-size: 14px;
}

.sideName{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    color: #95a5a6;
    font-size: 14px;
    margin: 10px 0 10px 0;
}

a{
    text-decoration: none;
    color: #464646;
    font-weight: 600;
}

.response-wrapper .match:hover{
    background-color: transparent;
}

.noth{
    
}

.night-mode-button{
    height: 50px;
    width: 50px;
    overflow: hidden;
    white-space:nowrap;
    transition: width 0.25s ease-out;
    cursor: pointer;
    background-color: #34495e;
    border-radius: 50px;
    box-shadow: rgba(0, 0, 0, 0.3) 0 1px 3px;
}

.night-mode-button:hover{
    width: 200px;
}

.night-mode{
    float: left;
    width: 50px;
    height: 50px;
    border-radius: 50px;
    background-color: #34495e;
    background-image: url('https://gamepedia.cursecdn.com/dota2_gamepedia/5/5e/Dark_Ascension_icon.png?version=cc3fa2e044ec044f873c789a234d760e');
    background-size: contain;
}

.night-name{
    line-height: 50px;
    margin-left: 60px;
}

.day .night-mode{
    background-image: url('https://gamepedia.cursecdn.com/dota2_gamepedia/a/aa/Supernova_icon.png?version=e6e78a7125e55fe6dac5f57f19866734');
}

.day{
    background-color: #ecf0f1;
}

.night-name:after{
    color: #ecf0f1;
    content: "Dark Ascension";
}

.day .night-name:after{
    color: #000;
    content: "Supernova";
}

.night{
    background-color: #000;
    color: white;
}

.night select{
    color: white;
}

.night option{
    color: #000;
}

.night .hero-select:hover{
    background-color: transparent;
}
